.hero
  position: relative
  // min-height: 800px
  // max-height: 800px
  // background-color: $bgc
  // color: $text
  //   box-sizing: border-box
  //   width: 100%
  //   display: flex
  //   justify-content: center
  // background-position: center
  // background-repeat: no-repeat
  // background-size: cover
  &::before
    content: ''
    position: absolute
    top: 0
    left: 0
    width: 100%
    height: 100%
    background-color: rgba($black, 0.5)
    // opacity: .6
    z-index: 1
  &__container
    position: relative
    z-index: 2
    height: 800px
  &__slider
    height: 100%
    // &-wrap
    //   height: 100%
    // margin-right: 80px
    // margin-bottom: 50px
    padding: 56px 0
    // padding-right: 2px
    // border-right: 1px solid $white
    position: relative
    &::before
      content: ''
      position: absolute
      height: 100%
      right: 2px
      top: 0
      bottom: 0
      width: 1px
      background: rgba($white, 0.5)
  &__slide
    // height: 100%
    // padding-right: 50px
    display: flex
    align-items: flex-end
    justify-content: space-between
    &-head
      max-width: 590px
      width: 100%
      display: flex
      flex-direction: column
      line-height: 0.77
      span
        // display: inline-block
        &:nth-child(even)
          margin-left: 70px

  &__clip
    position: absolute
    top: 0
    bottom: 0
    left: 0
    height: 800px
    width: 100%
    filter: blur(0px)
    background: url('../images/bg/hero.jpg') no-repeat center / cover
    // &::before
    //   position: absolute
    //   content: ''
    //   top: 0
    //   left: 0
    //   width: 100%
    //   height: 100%
    //   background: linear-gradient(90deg, #000000 0%, rgba(246, 246, 246, 0) 100%)
    //   opacity: 0.6
    //   z-index: 2
  &__video
    width: 100%
    height: 100%
    object-fit: cover
    // filter: blur(1px)

  &__pagination
    right: 0
    top: 0 !important
    width: 5px !important
    left: auto !important
    // span
  &__bullet
    display: block
    width: 5px
    background: transparent
    height: calc( 100% / var(--quantityHeroSlider) + 2px)
    border-radius: 5px
    transition: all 1s ease
    &_active
      background: $white


