:root {
  --widthScrollBar: 17px;
  --quantityHeroSlider: 10;
}

body {
  font-family: "Helvetica", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.85;
}

.heading {
  font-family: "Spectral", serif;
  font-weight: 700;
  font-size: 100px;
  line-height: 0.95;
  letter-spacing: -5px;
}

html {
  scroll-behavior: smooth;
}

body {
  color: #FFFFFF;
}
body._lock {
  overflow: hidden !important;
  height: 100% !important;
}

html,
body {
  height: 100%;
  margin: 0;
}

ul, li {
  padding: 0;
}

h1, h2, h3 {
  margin: 0;
}

input {
  outline: none;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  overflow: hidden;
  position: relative;
  /* flex-grow -   : 0 -, 1 -
  * flex-shrink -   : 0 -, 1 -
  * flex-basis -     
  * */
}
.wrapper > main {
  flex: 1 0 auto;
}

.footer {
  flex: 0 0 auto;
}

[class*=__container] {
  max-width: 1270px;
  margin: 0 auto;
  padding: 0 15px;
}

._ibg {
  position: relative;
}
._ibg img {
  object-fit: cover;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.logo--header {
  height: 50px;
}

.unvisible {
  position: absolute;
  top: 0;
  left: -3000px;
  overflow: hidden;
  width: 0;
  height: 0;
}

.header {
  padding: 27px 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 5;
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__logo {
  width: 94px;
  font-family: "Spectral", serif;
  font-weight: 700;
  font-size: 24px;
  line-height: 0.9166666667;
  color: #FFFFFF;
}
.lang {
  display: flex;
  align-items: center;
}
.lang__item {
  line-height: 2;
  color: #FFFFFF;
  width: 50px;
  height: 50px;
  border: 2px solid #A7FF4F;
  border-radius: 50%;
  padding: 9px 13px 10px 12px;
  text-transform: uppercase;
  cursor: pointer;
}
.lang__item:not(:last-child) {
  margin-right: 50px;
}
.lang__item._active {
  background: #A7FF4F;
  color: #000000;
}

.menu__lang {
  margin-bottom: -50px;
  z-index: 2;
  align-self: flex-end;
}
.menu__social {
  justify-content: flex-end;
  position: relative;
}
.menu__body {
  position: absolute;
  top: 0;
  right: -100%;
  width: 100%;
  min-height: 800px;
  padding: 125px 85px 50px;
  background: linear-gradient(180deg, #002535 0%, transparent 100%) #003851;
  opacity: 0.95;
  transition: right 0.5s ease-in 0s;
}
.menu__body._active {
  right: 0;
}
.menu__body._active::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, #002535 0%, transparent 100%) #003851;
  opacity: 0.7;
}
@media (max-width: 768px) {
  .menu__body {
    transition: right 0.3s ease-out 0s;
    padding: 100px 5% 50px 15%;
  }
}
@media (max-width: 576px) {
  .menu__body {
    min-height: 100vh;
  }
}
.menu__container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.menu__list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 30px;
}
.menu__list > li._active .menu__sub-list {
  opacity: 1;
  visibility: visible;
  height: auto;
  transition: all 0.5s ease;
}
.menu__item {
  position: relative;
  font-family: "Spectral", serif;
  font-weight: 700;
  font-size: 52px;
  line-height: 0.9423076923;
  color: #FFFFFF;
  text-transform: uppercase;
  transition: color 0.3s ease;
  letter-spacing: -1px;
}
.menu__item:not(:last-child) {
  margin-bottom: 39px;
}
@media (max-width: 768px) {
  .menu__item {
    margin-bottom: 40px;
    font-size: 32px;
  }
}
.menu__link {
  position: relative;
}
.menu__link::before {
  position: absolute;
  content: "";
  display: block;
  bottom: -6px;
  width: 0%;
  height: 2px;
  background-color: #A7FF4F;
  transition: width 0.5s ease;
}
.menu__link._active::before {
  width: 100%;
}
.menu__arrow {
  flex-shrink: 0;
  width: 30px;
  height: 30px;
  margin-left: 50px;
  border: none;
  background: url(../images/icons/sprite.svg#plus-white) center/contain no-repeat;
  outline: none;
  cursor: pointer;
  transition: transform 0.5s cubic-bezier(0.76, -0.76, 0.25, 1.26);
  color: #000000;
}
@media (max-width: 1280px) {
  .menu__arrow {
    margin-top: 8px;
  }
}
@media (max-width: 768px) {
  .menu__arrow {
    margin-left: 20px;
    margin-top: 0;
  }
}
.menu__arrow._active {
  transform: rotate(45deg);
}
.menu__submenu {
  display: flex;
  align-items: center;
}
@media (max-width: 1280px) {
  .menu__submenu {
    align-items: flex-start;
  }
}
@media (max-width: 992px) {
  .menu__submenu {
    flex-wrap: wrap;
  }
  .menu__submenu._active > ul {
    margin-top: 30px;
  }
}
.menu__sub-list {
  pointer-events: auto;
  display: flex;
  align-items: center;
  transition: all 0.3s ease-out;
  visibility: hidden;
  flex-wrap: wrap;
  opacity: 0;
  height: 0;
}
@media (max-width: 992px) {
  .menu__sub-list {
    width: 100%;
  }
}
.menu__sub-item {
  padding-left: 50px;
  font-weight: 700;
  font-size: 24px;
  line-height: 0.95;
  color: #FFFFFF;
  cursor: pointer;
  text-transform: none;
  padding-top: 5px;
  padding-bottom: 5px;
}
.menu__icon {
  display: block;
  position: relative;
  flex: 0 0 40px;
  width: 40px;
  height: 11px;
  border: none;
  background-color: transparent;
  cursor: pointer;
  outline: none;
  z-index: 5;
}
.menu__icon span {
  position: absolute;
  top: calc(50% - 3px);
  left: 0;
  width: 30px;
  height: 3px;
  background-color: #FFFFFF;
  transition: all 0.7s cubic-bezier(0.76, -0.76, 0.25, 1.26);
}
.menu__icon span:first-child {
  top: 0;
}
.menu__icon span:last-child {
  top: auto;
  bottom: 0;
  right: 0;
  left: auto;
}
@media (max-width: 768px) {
  .menu__icon span {
    transition: all 0.4s cubic-bezier(0.76, -0.76, 0.25, 1.26);
  }
}
.menu__icon._active {
  transition: position 0.5s ease-out 1s;
}
.menu__icon._active span:first-child {
  top: calc(50% - 3px);
  transform: rotate(-45deg);
}
.menu__icon._active span:last-child {
  top: calc(50% - 3px);
  transform: rotate(45deg);
  left: 0;
}

.social__list {
  display: flex;
}
@media (max-width: 768px) {
  .social__list {
    flex-wrap: wrap;
