.hero {
  position: relative;
}
.hero::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
}
.hero__container {
  position: relative;
  z-index: 2;
  height: 800px;
}
.hero__slider {
  height: 100%;
  padding: 56px 0;
  position: relative;
}
.hero__slider::before {
  content: "";
  position: absolute;
  height: 100%;
  right: 2px;
  top: 0;
  bottom: 0;
  width: 1px;
  background: rgba(255, 255, 255, 0.5);
}
.hero__slide {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
}
.hero__slide-head {
  max-width: 590px;
  width: 100%;
  display: flex;
  flex-direction: column;
  line-height: 0.77;
}
.hero__slide-head span:nth-child(even) {
  margin-left: 70px;
}
.hero__clip {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  height: 800px;
  width: 100%;
  filter: blur(0px);
  background: url("../images/bg/hero.jpg") no-repeat center/cover;
}
.hero__video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.hero__pagination {
  right: 0;
  top: 0 !important;
  width: 5px !important;
  left: auto !important;
}
.hero__bullet {
  display: block;
  width: 5px;
  background: transparent;
  height: calc(100% / var(--quantityHeroSlider) + 2px);
  border-radius: 5px;
  transition: all 1s ease;
}
.hero__bullet_active {
  background: #FFFFFF;
}

