html
  scroll-behavior: smooth
body
  //     
  @extend %body
  color: $text
  &._lock
    overflow: hidden !important
    height: 100% !important

html,
body
  height: 100%
  margin: 0

ul, li
  padding: 0
h1,h2,h3
  margin: 0
input
  outline: none

.wrapper
  display: flex
  flex-direction: column
  min-height: 100%
  overflow: hidden
  position: relative

  /*
   flex-grow -   : 0 -, 1 -
   flex-shrink -   : 0 -, 1 -
   flex-basis -     
   */
  > main
    flex: 1 0 auto

.footer
  flex: 0 0 auto

[class*='__container']
  max-width: $containerWidth + px
  margin: 0 auto
  padding: 0 $containerPadding + px

._ibg
  position: relative
  img
    object-fit: cover
    position: absolute
    width: 100%
    height: 100%
    top: 0
    left: 0

.logo--header
  height: 50px

.heading
  @extend %head

.unvisible
  position: absolute
  top: 0
  left: -3000px
  overflow: hidden
  width: 0
  height: 0
