.whoweare
  // height: 100vh
  background: $gradient-dark
  &__container
    // height: 100vh
    position: relative
    padding-top: 12.6%
    padding-bottom: 6%
    display: flex
    flex-direction: column
    justify-content: space-between
    &::before
      content: ''
      position: absolute
      top: 100px
      right: 0
      background: #BFD2D9
      filter: blur(150px)
      transform: rotate(-27.49deg)
      width: 500px
      height: 144px
      // background-color: rgba($bgc, 0.1)

    // &::after
    //   content: ''
    //   position: absolute
    //   bottom: 20px
    //   left: 20px
    //   width: 690px
    //   height: 206px
    //   background-color: rgba($bgc, 0.1)
  &__head
    margin-bottom: 14%
  &__info
    display: flex
    column-gap: 20px
    flex-wrap: wrap
    justify-content: flex-end
    // margin: 0 -15px
  &__text
    flex: 0 1 23.9%
    min-width: 295px
    border-top: 2px solid $white
      // padding: 0 25px
    padding-top: 10px
    padding-right: 3px
    p:not(:last-child)
      margin-bottom: 30px
